<template>

  <div v-if="carroproductos.length" class="">
    <div v-for="(carro, index) in carroproductos" :key="index" class="p-1">
      <div class="card">
        <div class="row">
          <div class="col-3">
            <img class="img-fluid" :src="carro.imagen" :alt="carro.nombre">
          </div>
          <div class="col-9 my-auto">
            <p class="m-0 p-0">{{ carro.nombre }}<br>
              $ {{ carro.precio_promo.toLocaleString() }}
            </p>
            <span class="btn" @click="remove(carro.id)">
              <i class="fa-solid fa-xmark"></i>
            </span>
          </div>
        </div>
      </div>
    </div>
    <hr>

    <div class="row">
      <div class="col-6 text-start text-white">
        Subtotal
      </div>
      <div class="col-6 text-end text-white">
        $ {{ total.toLocaleString() }}
      </div>
    </div>
    <div class="row">
      <div class="col-6 text-start text-white">
        Descuentos
      </div>
      <div class="col-6 text-end text-white">
        $ {{ descuento.toLocaleString() }}
      </div>
    </div>
    <div class="row fw-bold">
      <div class="col-6 text-start text-white">
        Total
      </div>
      <div class="col-6 text-success text-end text-white">
        $ {{ totalfinal.toLocaleString() }}
      </div>
    </div>
    <div class="d-grid gap-2 pt-3">
            <p class="text-white">Productos en el <i class="fa-solid fa-cart-shopping fa-2x text-white"></i>
        <span class="border border-white px-2 py-1 ms-2 rounded-circle">
          {{ carroProductos.length + carroPromociones.length + carroAccesorios.length }}
        </span>
        </p>
      <button @click="pagar" class="btn btn-dark d-inline-block">Pagar</button>
    </div>
  </div>

  <div v-else-if="carroProductos.length || carroPromociones.length || carroAccesorios.length" class="">
    <div class="d-grid gap-2 pt-3">
      <p class="text-white">Productos en el <i class="fa-solid fa-cart-shopping fa-2x text-white"></i>
        <span class="border border-white px-2 py-1 ms-2 rounded-circle ">
          {{ carroProductos.length + carroPromociones.length + carroAccesorios.length }}
        </span>
      </p>
      <button @click="pagar" class="btn btn-dark d-inline-block">Pagar</button>
    </div>
  </div>

  <div v-else class="text-white">
    No hay productos seleccionados
  </div>


</template>

<script>

  import {mapState} from "vuex";

  export default {
    name: "CarroCompra",
    props: {
      productos: Array,
      carroproductos: Array,
      tipo: String, 
      total: 0,
      descuento: 0,
      totalfinal: 0,
    },
    methods: {
      remove(id){
        this.$store.commit('removeCarro', [id, this.tipo])
      },
      pagar() {
        this.$store.commit('pagar')
        this.$router.push('checkout')
      }
    },
    computed: {
      ...mapState(['carroProductos', 'carroPromociones', 'carroAccesorios'])
    }
  }
</script>

